<template>
    <v-container fluid class="bg">
        <div class="login-ctn">
            <h1 class="title-ctn">
                <img src="../assets/img/logo.png" alt="dona lola">
            </h1>
            <v-form class="ctn-login">
                <v-text-field
                  v-validate="'required|email'"
                  prepend-icon="account_circle"
                  :error-messages="errors.collect('email')"
                  v-model="email"
                  label="E-mail"
                  data-vv-name="email"
                  required
                ></v-text-field>
                <v-text-field
                  v-model="password"
                  prepend-icon="lock"
                  :append-icon="show1 ? 'visibility_off' : 'visibility'"
                  v-validate="'required'"
                  :error-messages="errors.collect('password')"
                  :type="show1 ? 'text' : 'password'"
                  name="password"
                  label="Password"
                  @click:append="show1 = !show1"
                ></v-text-field>
                <p v-if="error">{{error}}</p>
                <v-flex xs12 sm6 text-center style="margin-bottom:10px;">
                    <v-btn
                        style="color:#fff"
                        color="teal"
                        @click="submit"
                    >
                        INICIAR SESION
                    </v-btn> <br>
                    <a href="#" style="color:#42A5F5;text-decoration: none;"><b>Crea tu usuario</b></a> <br>
                    <a href="#" style="color:#42A5F5;text-decoration: none;">Olvide mi contraseña</a>
                </v-flex>
            </v-form>
        </div>
    </v-container>
</template>
<script>
import { mapActions } from 'vuex'
export default {
  name: 'Login',
  data () {
    return {
      response: '',
      password: '123456',
      email: 'jeanpaul1304@gmail.com',
      show1: false,
      dictionary: {
        custom: {
          email: {
            required: 'Ingrese su correo'
          },
          password: {
            required: 'Ingrese su contraseña'
          }
        }
      }
    }
  },
  mounted () {
    this.$validator.localize('en', this.dictionary)
  },
  methods: {
    submit () {
      // this.$validator.validateAll()
      let email = this.email
      let password = this.password
      this.error = ''
      this.login({ email, password }).then((data) => {
        window.c = firebase.auth().currentUser
        debugger
        firebase.auth().currentUser.getIdToken().then((data) => {
          this.setToken(data)
          // console.log(firebase.auth().userInfo().getEmail())
          let currentUser = firebase.auth().currentUser
          this.$router.push('home')
        })
      }).catch((error) => {
        this.error = 'Ingrese credenciales validas'
        return error
      })
    },
    ...mapActions([
      'login',
      'setToken'
    ])
  }
}

</script>

<style lang="stylus" scoped>
  .bg
    padding 0
  .login-ctn
    background url('../assets/img/bg.jpg')
    padding-top 10vh
    position: absolute
    height: 100%
    width: 100vw
    left: 0
    right: 0
    background-size: cover
    padding-top: 1vh
    padding-bottom: 4vh
    overflow: auto
  .title-ctn
    text-align center
    display inline-block
    width 100%
    height auto
    img
      width 125px
      border-radius 5px
      overflow hidden
  .ctn-login
    background rgba(255, 255, 255, .8)
    border-radius 10px
    width 90%
    margin 0 auto
    padding 22px 12px 2px 12px
  .text-center
    text-align center
    .login-link
      line-height 1.5
      display inline-block
      &.first
        margin-top 15px

</style>
